<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t(HeaderText) }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG"
                                role="button" aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div v-if="$parent.groupId>0" id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgHiringGroupListEdit')"></div>
                                </div>
                                <div v-else id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgHiringGroupListAdd')"></div>
                                </div>
                            </div>
                        </span>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props:{
        HeaderText:{
            Type:String,
            Required:true
        },
    },
    data() {
        return {
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                    {
                            astype: "TextField",
                            label: this.$t('GroupName'),
                            name: "GroupName",
                            value: "",
                            placeholder: this.$t('GroupName'),
                            validationRules: {
                                "required": true
                            },
                            config: {
                                onChange: this.IsGroupExists,
                            },
                                                        
                            disabled: false,
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "CustomSelectField",
                            mode: "tag",
                            label: this.$t('Recruiters'),
                            name: "Recruiters",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select Recruiters'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ]
                }
            ],
            isLoading: false,            
            CompanyId:null,
            Userid:null,
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible:true,                    
                    onClick: this.OnSubmit
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-send pr-2'></i>Update",
                    isVisible:false, 
                    onClick: this.OnSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
        }
    },
    created:async function () {        
        var vm=this;
        vm.CompanyId= this.GetUserInfo.CompanyId;
        vm.Userid= this.GetUserInfo.ID;
        await vm.GetRecruiters();
        if(vm.$parent.groupId>0){ 
            vm.buttons[0].isVisible=false;
            vm.buttons[1].isVisible=true;
            await vm.FetchAddEditGroupData(vm.$parent.groupId);
        }
        else{
            vm.buttons[0].isVisible=true;
            vm.buttons[1].isVisible=false;
        }
    },
    methods: {
        FetchAddEditGroupData:async function(ths){            
            var vm=this;        
            var params=`groupid=${ths}`;
            await DataServices.GroupListing(params).then((response) => { 
                if(response.data != ''){                     
                    var recruiterArray=[];            
                    vm.FormSchema[0].Data[0].value=response.data.data[0].group_name;  
                    response.data.recruiterData.forEach(element => {
                        let obj={
                            label:element.name,
                            value:element.value
                        }
                        recruiterArray.push(obj);
                    });
                    vm.FormSchema[1].Data[0].value=recruiterArray;
                }
            })
        },
        GetRecruiters:async function(){
            var vm=this;            
            var params=`moduleName=Hiring`;
            await DataServices.GetRecruiterList(params).then((response) => {
                vm.isLoading = false;
                if(response.data.DATA.length>0){                    
                    vm.FormSchema[1].Data[0].config.options=response.data.DATA;
                }
            })
        },
        async onSubmit(formfields, val) {                        
            var vm = this;  
            vm.isLoading =true;        
            var jsonobj = {
                UserId: vm.Userid,
                CompanyId: vm.CompanyId,
                GroupID: vm.$parent.groupId==undefined?0:vm.$parent.groupId,
                Nametext: formfields.GroupName,
                RecruiterID: formfields.Recruiters,
                Groupdate:  new Date()
            };            
            var params = JSON.stringify(jsonobj);
            await DataServices.SaveRecruiterGroup(params).then((response) => {   
                vm.isLoading =false;              
                if (response.data[0].Status == 'Success') {
                    if(vm.$parent.groupId>0){
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Group")]), "success", true, vm.$t("Alert"));
                    }else{
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Group")]), "success", true, vm.$t("Alert"));
                    }                   
                    vm.Close();
                    vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Group")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        },
        Close: function () {
            this.$parent.IsGroupOpen=false;
        },
        IsGroupExists:async function(){            
            var vm=this;
            vm.isLoading = true;
            var params=`groupname=${this.FormSchema[0].Data[0].value}`;
            await DataServices.GroupExists(params).then((response) => { 
                if(response.data=="exists"){
                    vm.isLoading = false;
                    vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("GroupName")]), "warning", true, vm.$t("Alert"));
                }else{
                    vm.isLoading = false;
                }
            })
        }
    }
}
</script>