<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="HR" SubModuleCode="Group" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
            IdentityColumn="group_id" :listType="['List']" :ListData="GroupData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            IsSearchFieldsOnTop="true" SearchPosition="top" :ListDataCallBackFunction="FetchData" :showCheckBox="true"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            
        </tg-list>
        <AddGroup v-if="IsGroupOpen" :HeaderText="HeaderText"></AddGroup>
    </section>
</template>
<script>
import Schema from "./Schema"
import DataServices from "@/services/DataService";
import AddGroup from './OpwnGroupModel.vue'
import moment from 'moment'
export default {
    components: { AddGroup },
    data() {
        return {
            isLoading: false,
            IsGroupOpen: false,
            Headers: [{
                "COLUMN_NAME": "group_name",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Group Name',
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "recruiter_id",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Team',
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "created_at",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Add/Update On',
                "DISPLAY_ORDER": 3,
            }
            ],
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName:"groupname",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            groupid: 0,
            groupname: '',
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0, 
            noRecords:false,          
            searchCondition:'',
            GroupData: [],
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            CompanyId: null,
            Userid: null,
            HeaderText: ''
        }
    },
    created: async function () {
        var vm = this;
        vm.CompanyId = this.GetUserInfo.CompanyId;
        vm.Userid = this.GetUserInfo.ID;
        await vm.FetchData();
    },
    updated() {
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white') {
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t('GroupListing');

            divElement[0].appendChild(spanElement);
        }
    },
    mounted() {
        var ths = this;
        ths.leftSearchFields.forEach(item => {
            item.value = '';
            item.isSearch = false;
        })
    },
    methods: {
        actionButtonClick: function (callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case "EditGroup":
                    this.openAddGroup(item.group_id, 'EditGroup');
                    break;
            }
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openAddGroup":
                    this.openAddGroup(0, 'AddGroup');
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {            
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        FetchData: async function () {
            var vueObj = this;
            vueObj.isLoading = true;
            var params = `groupid=${vueObj.groupid}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&${vueObj.searchCondition}`
            await DataServices.GroupListing(params).then((response) => {
                if (response.data && response.data.data.length > 0) {
                    vueObj.isLoading = false;
                    vueObj.GroupData = response.data.data;
                    vueObj.GroupData.forEach((item) => {
                        if (item.created_at) {
                            item.created_at= vueObj.formatTimestamp(item.created_at)
                        }
                    })
                    vueObj.TotalRecords = response.data.data[0].totalrecords;
                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                    vueObj.CurrentPage = vueObj.PageNumber;
                    vueObj.listheaderbuttons[1].isdisabled = true;
                    vueObj.noRecords = false;
                }else {
                    vueObj.GroupData=[];
                    vueObj.noRecords = true;
                    vueObj.TotalRecords = 0
                    vueObj.isLoading = false;
                }
            })
        },
        formatTimestamp: function (input) {                        
            const date = moment(input);
            const formattedDate = date.format('MM/DD/YYYY h:mm A');
            return formattedDate;
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.CompanyId,
                        userId: vm.Userid,
                        moduleName: "HIRING",
                        subModuleCode: "HR_RECRUITER_GROUP"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.HRCommonDelete(objectPostString).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Group')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Group')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        openAddGroup: function (groupId, TextHeader) {
            this.groupId = groupId;
            this.HeaderText = TextHeader;
            this.IsGroupOpen = true;
        }
    }
}
</script>
